@keyframes rotation {
  0% { transform: rotateY(90deg); }
  100% { transform: rotateY(0deg); }
}

@keyframes rotation-back {
  0% { transform: rotateY(0deg); }
  100% { transform: rotateY(90deg); }
}

@keyframes slide-in {
  from { left: 100%; }
  to { left: 0; }
}

@keyframes slide-out {
  from { left: 0; }
  to { left: 100%; }
}

.card-actions {
  font-weight: normal;
  font-size: 13px;
}

.promo {
  background: #f44;
  position: absolute;
  right: 0;
  border-radius: 6px;
  font-weight: bold;
  color: white;
  padding: 0px 4px;
  font-size: large;
  margin-right: 10px;
  margin-top: -63px;
}

.btn-disabled {
  border: none;
}

.unavailable {
  background: #d5d6d9;
  color: #b0b3b9;
  position: absolute;
  right: 0;
  border-radius: 6px;
  padding: 0px 4px;
  font-size: 14px;
  margin-right: 6px;
  margin-top: -29px;
  z-index: 1;
}

.price {
  font-weight: bold;
}

.price .old {
  position: relative;
  color: #99a3ae;
  font-size:small;
}

.price .old::before {
  background: #99a3ae;
  content: "";
  display: block;
  height: 1px;
  left: 0;
  position: absolute;
  top: 50%;
  transform: rotate(-3deg);
  width: 100%;
}

.price .active {
  color: rgb(248, 17, 85);
}

.card-description {
    position: absolute;
    left: 100%;
    width: 100%;
    height: 100%;
    display: flex;
    text-align: left;
    word-break: normal;
    font-size: 11px;
    padding: 1rem;
    color: var(--tg-theme-text-color);
    background-color: var(--tg-theme-bg-color);
}

.card-description.active{
    animation-name: slide-in;
    animation-duration: 0.5s;
    left: 0;
    /*transform: rotateY(0deg);*/
}

.card-description.passive{
    animation-name: slide-out;
    animation-duration: 0.5s;
    left: 100%;
}

.dots {
    position: absolute;
    bottom: 10px;
    display: flex;
    justify-content: center;
}

.dots .container {
  display: flex;
  width: fit-content;
  background: rgba(220,220,220, 0.7);
  padding: 2px 5px;
  border-radius: 20px;
}

.dots .dot {
    background: rgba(0,26,52,.2);
    margin-left: 2px;
    border-radius: 100%;
    width: 5px;
    height: 5px;
}

.dots .dot:first-child {
    margin-left: 0px;
}

.dots .dot.active {
    background-color: var(--tg-theme-button-color);
}